import { useState, useEffect } from 'react'

import './gif.css'

const Gif = ({image, model}) => {

      const [gif, setGif] = useState([])
      const [size, setSize] = useState([])

      const getGif = (img) => {

            let new_img = new Image()
            let obj = {} 
            let objSize = {}
            let width = null 
            let height = null

            new_img.onload = () => {
                  width = new_img.naturalWidth 
                  height = new_img.naturalHeight 

                  objSize.width = width 
                  objSize.height = height
                  objSize.seconds = width / height 
                  objSize.steps = (width / height) - 1 
                  setSize(objSize)
            }

            new_img.src = img
            obj.src = new_img.src
            setGif(obj)
      }

      useEffect(() => {
            getGif(image)
      }, [image])
      
      if(size.length === 0) return

      const style = {
            backgroundImage: `url(${gif.src})`,
            backgroundPosition: 'left top',
            animationName: 'loop_gif',
            animationDuration: `${size.seconds / 10}s`,
            animationTimingFunction: `steps(${size.steps})`,
            animationIterationCount: 'infinite'
      }

      return (
            <div className="gif_card">
                  <div className="gif_img">
                        <div className="gif_img__box" style={style}></div>
                  </div>
                  <div className="gif_model">
                        <strong>{model}</strong>
                  </div>
            </div>
      )
}

export default Gif
